import React from "react";
import { Html, useProgress } from "@react-three/drei";

import image from "../common/overwatch-logo.png";

const Loader = () => {
  const { progress } = useProgress();
  let result = 100 - Math.floor(progress);
  return (
    <Html center>
      <img className="loader__img" style={{ filter: `grayscale(${result}%)`, width: `300px`}} src={image} alt="loader-img" />{" "}
    </Html>
  );
};

export default Loader;
