import "./App.css";
import { Suspense, useState, useEffect } from "react";
import { Canvas } from "@react-three/fiber";
import { OrbitControls } from "@react-three/drei";
import Sound from "react-sound";
import Model from "./components/Model";
import Loader from "./components/Loader";
import ambientSound from "./common/ambient.mp3";
import lootBox from "./common/lootBox.m4a";
import BoxText from "./components/BoxText";

function App({ handleSongLoading, handleSongPlaying, handleFinishedPlaying }) {
  const [isActive, setActive] = useState(false);
  const [isLoaded, setLoaded] = useState(false);
  const [tPos, setTPos] = useState(0);

  const buttonHandler = () => {
    setActive(!isActive);
    setTPos(1)
  };

  console.log(tPos);
  return (
    <div className="app__wrapper">
      <Canvas shadows>
        <Suspense fallback={<Loader />}>
          <Model setLoaded={setLoaded} isActive={isActive} />
          <OrbitControls />
        </Suspense>
      </Canvas>
      <Sound
        url={lootBox}
        playStatus={isActive ? Sound.status.PLAYING : Sound.status.STOPPED}
        onLoading={handleSongLoading}
        onPlaying={handleSongPlaying}
        onFinishedPlaying={handleFinishedPlaying}
        muted
      />
      {isLoaded ? (
        <div className="app__button" onClick={() => buttonHandler()}>
          Open loot box
        </div>
      ) : null}
      {/* <BoxText tPos={tPos} /> */}
    </div>
  );
}

export default App;
