import { useEffect, useRef } from "react";

import { useFrame, useLoader } from "@react-three/fiber";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import { LoopOnce, Mesh } from "three";

import * as THREE from "three";
import { PerspectiveCamera, useAnimations } from "@react-three/drei";

import modelPath from "./models/lootbox1.glb";

export default function Model({ isActive, setLoaded }) {
  const group = useRef();
  const gltf = useLoader(GLTFLoader, modelPath);

  useEffect(() => {
    gltf.scene.traverse((object) => {
      if (object instanceof Mesh) {
        object.castShadow = true;
        object.receiveShadow = true;
      }
      gltf.nodes.Circle.castShadow = false;
    });
    setLoaded(true);
  }, [gltf]);

  let mixer;
  if (gltf.animations.length && isActive) {
    mixer = new THREE.AnimationMixer(gltf.scene);
    gltf.animations.forEach((clip) => {
      const action = mixer.clipAction(clip);
      action.setLoop(LoopOnce);
      action.clampWhenFinished = true;
      action.play();
    });
  }
  useFrame((state, delta) => {
    mixer?.update(delta);
  });

  const { actions } = useAnimations(gltf.animations, group)

  useEffect(() => {
    if (isActive) {
      let actionCam = actions["CameraAction"];
      actionCam.setLoop(LoopOnce);
      actionCam.clampWhenFinished = true;
      actionCam.play();
    }
  }, [isActive]);

  const pos = gltf.cameras[0].position || [0,0,0]
  return (
    <>
      <PerspectiveCamera
        ref={group}
        name="Camera"
        makeDefault={true}
        fov={50}
        position={pos}
      />
      <spotLight
        color={[1, 1, 1]}
        position={[-6, 8, 2]}
        intensity={2}
        castShadow
      />
      <spotLight color={[1, 1, 1]} position={[0, 5, -3]} intensity={1} />
      <ambientLight
        color={[0.1, 0.3, 0.6]}
        position={[0, 0, -8]}
        intensity={1}
      />
      <color args={[0.1, 0.3, 0.6]} attach="background" />
      <primitive ref={group} object={gltf.scene} />;
    </>
  );
}
